@color: #f65c8f;
body {
    background-color: #ECEBEB;
}
.header {
    padding: 0;
    text-align: center;
    background-color: @color;
    position: relative;
    .title {
        margin-bottom: 0;
        line-height: 45px;
        margin-top: 0;
        color: white;
        letter-spacing: 3px;
        font-size: 16px;
    }
    .header-left {
        position: absolute;
        width: 60px;
        left: 0;
        top: 0;
        p {
            margin-bottom: 0;
            font-size: 13px;
            line-height: 45px;
            margin-left: 15px;
            color: white;
            display: inline;
        }
        img {
            width: 15px;
            display: inline;
            margin-top: -2px;
        }
    }
    .header-right {
        height: 45px;
        width: 60px;
        text-align: right;
        position: absolute;
        right: 0;
        top: 0;
        img {
            width: 16px;

            margin-right: 15px;
        }
    }
}
.slider {
    padding: 0;
    .am-control-nav {
        li {
            margin: 0 2px;
            a {
                width: 5px;
                height: 5px;
                background-color: white;
            }
        }
    }
    .am-slider-a1 .am-control-nav li a.am-active {
        background-color: @color;
    }
}
.product-name {
    padding-top: 5px;
    padding-bottom: 0px;
    background-color: white;
    .product-name-p {
        height: 37px;
        overflow: hidden;
        p {
            font-size: 14px;
            color: #6D6D6D;
            margin: 0;
            line-height: 18px;
        }
    }
}

.product-price {
    padding-bottom: 5px;
    background-color: white;
    .product-price-big {
        color: @color;
        font-size: 19px;
        display: inline-block;
        height: 20px;
        line-height: 20px;
    }
    .product-price-sm {
        color: #ccc;
        font-size: 12px;
        display: inline-block;
        height: 13px;
        line-height: 13px;
    }
}

.am-divider {
    margin: 0 auto;
}

.merchant {
    background-color: white;
    border-bottom: 1px solid #ccc;
    box-shadow: 0 2px 2px #ccc;
    p{
        font-size: 12px;
        color: #EA7219;
        margin: 0;
        line-height: 23px;
    }
}

.parameter {
    background-color: white;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    box-shadow: 0px -2px 2px #ccc;
    padding-top: 11px;
    padding-bottom: 11px;
    margin-top: 7px;
    p {
        float: left;
        font-size: 13px;
        margin: 0;
        line-height: 18px;
        color: #6D6D6D;
    }
    img {
        float: right;
        width: 8px;
    }
}

.go-to-detail {
    background-color: white;
    border-bottom: 1px solid #ccc;
    box-shadow: 0px 2px 2px #ccc;
    padding-top: 11px;
    padding-bottom: 11px;
    p {
        float: left;
        font-size: 13px;
        margin: 0;
        line-height: 18px;
        color: #6D6D6D;
    }
    img {
        float: right;
        width: 8px;
    }
}

.guessing {
    padding: 6px;
    margin-top: 7px;
    background-color: white;
    border-top: 1px solid #ccc;
    box-shadow: 0px -2px 2px #cccccc;
    .guessing-title {
        padding: 6px;
        p {
            font-size: 13px;
            margin: 0;
            line-height: 18px;
            color: #6D6D6D;
        }
    }
    .guessing-list {
        padding: 6px;
        .guessing-list-main {
            img {
                border: 1px solid #ccc;
                width: 100%;
            }
            .guessing-list-name {
                padding: 5px 0px;
                width: 100%;
                height: 31px;
                overflow: hidden;
                margin-bottom: 3px;
                p {
                    font-size: 12px;
                    line-height: 13px;
                    margin: 0;
                }
            }
            .price {
                height: 18px;
                p {
                    font-size: 14px;
                    color: @color;
                    margin: 0;
                    line-height: 18px;
                }
            }
        }
    }
}

#detail-main {
    
    .am-popup-hd {
        border-bottom: none;
        background-color: @color;
        h4 {
            font-weight: 400;
            font-size: 15px;
            color: white;
             
        }
        .am-close {
            opacity: 1;
            color: white;
        }
    }
    .am-popup-bd {
        padding: 0;
        .am-tabs {
            margin: 0;
        }
        .am-tabs-default .am-tabs-nav>.am-active a {
            background-color: white;
            color: @color;
        }
    }
}